
import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { getTrendingMedia } from '../../services/tmdbService';
import MediaCard from '../MediaCard';
import SeeMoreCard from '../SeeMoreCard';
import '../../styles/Carousel.css';
import useHorizontalScroll from '../../hooks/useHorizontalScroll';

const Carousel = ({ items, type }) => {
    const carouselRef = useHorizontalScroll();

    return (
        <div className="carousel-container" ref={carouselRef}>
            <div className="carousel-wrapper">
                {items.map(item => (
                    <div className="carousel-item" key={item.id}>
                        <MediaCard item={item} type={type} />
                    </div>
                ))}
                <div className="carousel-item">
                    <SeeMoreCard type={type} />
                </div>
            </div>
        </div>
    );
};

Carousel.propTypes = {
    items: PropTypes.array.isRequired,
    type: PropTypes.string.isRequired,
};

const Trending = ({ mediaType }) => {
    const [trending, setTrending] = useState([]);

    useEffect(() => {
        const fetchTrending = async () => {
            const trendingData = await getTrendingMedia(mediaType);
            setTrending(trendingData || []);
        };

        fetchTrending();
    }, [mediaType]);

    return <Carousel items={trending} type={mediaType} />;
};

Trending.propTypes = {
    mediaType: PropTypes.string.isRequired,
};

export default Trending;
